


* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}


body {
    padding: 5em;
    /* background: #ccc; */
}

.app {
    display: flex;
    flex-direction: column;
    width: 45em;
}

.app__title {
    font-size: 2.2em;
    margin-bottom: 1em;
    color: rgb(94, 94, 254);
}

.app__top__btn, .list__btn, .list__btn-2{
    padding: .85em 2.3em;
    background: rgb(94, 94, 254);
    font-weight: 900;
    color: white;
    border: 0;
    outline: 0;
    border-radius: .5em;
    cursor: pointer;
    margin-bottom: 2em;
}

.list__btn, .list__btn-2 {
    margin-bottom: 0!important;
}

.list__btn-2 {
    margin-left: 1em;
    background-color: red;
}

.app__top {
    /* padding: 1em;
    border: 1px solid rgb(141, 141, 229);
    border-radius: .5em; */
}

.cards {
    padding: 1.3em 1em;
    border: 1px solid rgb(141, 141, 229);
    border-radius: .5em;
}

.list {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: .2em 1em;
    border-radius: .5em;
    background-color: rgba(96, 96, 246, 0.124);
    color: rgb(0, 0, 0);
    font-weight: 900;
    margin-bottom: 1em;
    min-height: 3em;
    font-size: 1.12em;
    width: 100%;
    /* white-space: wrap; */

}

.para {
    font-style: italic;
    width: 22em;
    margin-right: 1em;
    white-space: wrap;
}

.form {
    padding: 2em 0;
}

.text {
    margin-right: 1em;
    padding: 1em 1em;
    border-radius: .5em;
    outline: 0;
    border: 1px solid rgb(141, 141, 229);
    background: rgb(238, 238, 252);
}

.form__btn {
    padding: 1em 2em;
    background: rgb(94, 94, 254);
    font-weight: 900;
    color: white;
    border: 0;
    outline: 0;
    border-radius: .5em;
    cursor: pointer;
    margin-bottom: 2em;
    transition: all ease-in-out .5s;
}

.form__btn:hover {
    background: rgb(141, 141, 229);
}

.message {
    background: red;
    color: white;
    font-weight: 800;
    font-size: 1.1em;
    width: 17em;
    padding: 1em;
    border-radius: .5em;
    animation: nas 0.4s ease-in-out;
}

@keyframes nas {
    0% {transform: translate(0);}
    30% {transform: translate(-30px);}
    70% {transform: translate(40px);}
    100% {transform: translate(0);}
}